  font-family: 'ProximaNova-Bold';
  src: url("../fonts/ProximaNova-Bold.otf"); }

@font-face {
  font-family: 'ProximaNova-Light';
  src: url("../fonts/ProximaNova-Light.otf"); }

@font-face {
  font-family: 'ProximaNova-Regular';
  src: url("../fonts/ProximaNova-Regular.otf"); }

